import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from "@/store";
import VueBarcodeScanner from 'vue3-barcode-scanner'
// inject barcode scanner with option (add sound effect)
// sound will trigger when it's already scanned

const app = createApp(App)
let options = {
    sound: true, // default is false
    soundSrc: '/stock_vue/static/beep.mp3', // default is blank
    sensitivity: 100, // default is 100
    requiredAttr: false, // default is false
    controlSequenceKeys: ['NumLock', 'Clear'], // default is null
    callbackAfterTimeout: true // default is false
}

app.use(store)
app.use(router)
app.use(VueBarcodeScanner, options)
app.mount('#app')