<template>
  <div class="row" v-if="user_data">
    <div class="col">
      <div class="card">
        <div class="card-body">
          <h4 class="card-title ">Пользователь: {{ user_data.user_data.fullname }}</h4>
          <b class="text-primary" style="color: yellow">{{ user_data.user_data.store_title }}</b>
        </div>
      </div>
    </div>
  </div>

  <div class="wrap">
    <div class="row">
  <div class="col-6" v-for="type in picking_types" :key="type.id">
    <router-link :to="{name: 'PickingType', params:{pick_type: type.id}}">
      <div class="card">
        <div class="card-body">
          <h5 class="card-title">{{ type.name }}<span class="mb-1"><b
              class="text-primary float-right"
              style="color: yellow">{{ type.count_picking_ready }}</b>
            </span></h5>
          <p class="card-description">{{type.description}}</p>
        </div>
      </div>
      </router-link>
    </div>
  </div>
  </div>


</template>

<script>
import lavkach_api from "@/lavkach_api";
import {user} from "@/lavkach_api";

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'Main',
  data: function () {
    return {
      user_data: user,
      picking_types: false,
      packman_show: false,
      orders: this.$store.state.orders
    }
  },
  async mounted() {
    this.packman_show = true
    const data = await lavkach_api.methods.mainView();
    this.picking_types = data.picking_types;
    this.packman_show = false
    lavkach_api.methods.poll()
    console.log(this.orders)

  },
}
</script>

