import {createStore} from 'vuex'

const store = createStore({
    state: {
        orders: []
    },
    mutations: {
        ordersUpdate(state, vals) {
            if (vals) {
                // Если есть значения
                for (let key in vals) {
                    if (vals[key].state === 'assigned') {
                        //Если пикинг в нужном статусе обновляем/добавляем
                        state.orders[key] = vals[key]
                        console.log('Обновляем пикинг #' + key)
                    } else {
                        // если в не нужно удаляем
                        if (state.orders[key]) {
                            delete state.orders[key]
                            console.log('Удаляем пикинг #' + key)
                        }
                    }

                }
            }
        }
    },
    getters: {
        ordersByTypeId: state => (pt) => {
            console.log(pt)
            return state.orders.filter(picking => picking.picking_type_id.id == pt)
        },
        ordersById: state => (pk) => {
            console.log(pk)
            return state.orders[pk]
        },
        stockMoveById: state => (params) => {
            console.log(params)
            return state.orders[params['pk']]['move_lines'][params['sm']]
        }
    }

})

export default store