        <div class="card-body">
          <div class="d-flex flex-row align-items-center">
            <h6 v-bind:key="current_step" class="text-white">{{ current_step.display_name }}
              {{ stock_move[current_step.name].name }}</h6>
          </div>
        </div>
      </div>
    </div>
  </div>

  <div class="row" v-if="current_step">
    <div class="col">
      <div class="card form-control" v-if="current_step.input">
        <div class="card-body">
          <div v-if="['integer', 'float'].indexOf(current_step.input_type)">
            <div class="input-group">
              <div class="input-group-append">
                <button v-on:click="buttonMinus" class="btn btn-sm btn-gradient-primary form-control" type="button">
                  <b>-</b></button>
              </div>
              <input type="number" class="input-move form-control"
                     v-model.number="stock_move[current_step.input]">
              <div class="input-group-append">
                <button v-on:click="buttonPlus" class="btn btn-sm btn-gradient-primary form-control" type="button">
                  <b>+</b>
                </button>
                <div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

  </div>
  <div class="row" v-if="current_step">
    <div class="col">
      <div class="card form-control" v-if="current_step.input">
        <div class="card-body">
          <div class="input-group">
            <button v-on:click="manualNextStep" class="btn btn-lg btn-gradient-success form-control" type="button"><b>Continue</b>
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>

