<script>
//import lavkach_api from "@/lavkach_api";
import lavkach_api from "@/lavkach_api";

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'StockMove',
  data: function () {
    return {
      scanner: undefined,
      qty: 0,
      min: 0,
      max: 999,
      steps: undefined,
      current_step: undefined
    }
  },
  created () {
    // Add barcode scan listener and pass the callback function
    this.$barcodeScanner.init(this.onBarcodeScanned)
  },

  watch: {
    async scanner() {
      console.log('before action:' + this.scanner)
      if (this.scanner) {
        //
        // if (this.current_step['name'] === 'result_package_id'){
        //   const payload = {'pack_name': this.scanner, 'location_id': this.stock_move.location_id.id}
        //   const data = await lavkach_api.methods.get_or_create_package(payload)
        //   this.stock_move.result_package_id = data.pack
        //   this.current_step['status'] = true
        //   this.setCurrentStep()
        //   console.log(data)
        //   console.log('set: undefined')
        //   this.scanner = undefined
        //   return
        // }
        const step_obj = this.stock_move[this.current_step.name]
        if (this.scanner === step_obj.barcode) {
          if (this.current_step.input) {
            this[this.current_step.input]++
          } else {
            this.current_step.status = true
            this.setCurrentStep()
            console.log(this.current_step)
          }
        } else {
          if (this.current_step.can_obj_change) {
            const payload = {
              'scan_value': this.scanner,
              'object_name': this.current_step.name,
              'can_obj_create': this.current_step.can_obj_create
            }
            const data = await lavkach_api.methods.get_or_create_object(payload)
            this.stock_move[this.current_step.name] = data[this.current_step.name]
            this.current_step.status = true
            console.log(data)
            this.setCurrentStep()
          }
        }
        console.log('set: undefined')
        this.scanner = undefined
      }
    },
  },
  async mounted() {
    this.packman_show = false;
    this.steps = this.stock_move.steps
    this.setCurrentStep()
    console.log(this.current_step)
  },
  computed: {
    stock_move() {
      const params = {'pk': this.$route.params.pk, 'sm':this.$route.params.sm}
      return this.$store.getters.stockMoveById(params)
    }
  },
  methods: {
    async buttonPlus() {
      this.stock_move[this.current_step.input]++
      console.log('Input ++: ')
    },
    async buttonMinus() {
      this.stock_move[this.current_step.input]--
      console.log('Input --: ')
    },
    async manualNextStep() {
      this.current_step.status = true
      console.log('Manual step pass')
      this.setCurrentStep()
    },
    async done() {
      console.log('Done')
      let doneData = {
        'picking_id': this.stock_move.picking_id,
        'stock_move_id': this.stock_move.id,
        'product_id': this.stock_move.product_id.id,
        'location_id': this.stock_move.location_id.id,
        'location_dest_id': this.stock_move.location_dest_id.id,
        'qty_done': this.stock_move.qty_done,
        'result_package_id': this.stock_move.result_package_id.id
      }
      const data = await lavkach_api.methods.stockMoveLineDone(doneData);
      console.log(data)
      this.$router.push({name: 'Picking', params: {pk: this.stock_move.picking_id}})
    },

    async setCurrentStep() {
      this.current_step = false
      for (let key in this.steps) {
        let step = this.steps[key]
        if (!step['status']) {
          this.current_step = step
          console.log('SET: ', step)
          break
        }
      }
      if (!this.current_step) {
        this.done()
      }
    },
    onBarcodeScanned (barcode) {
      console.log(barcode)
      // do something...
    },
  }
}

</script>

