import axios from "axios";
import store from "@/store";

export let user = false;


export default {
    url: false,
    name: 'lavkach_api',
    pollCursor: false,
    methods: {
        async setUrl(url) {
            this.url = url
        },
        async login(qr) {
            const headers = {'Authorization': 'Token \t' + 'dsadsadas'}
            const loginUrl = this.url + '/app/v1/login/';
            const response = await axios.post(loginUrl, {'qr': qr}, {headers})
                .catch(function (error) {
                    response.errors = error.toJSON()
                    console.log(error.toJSON());
                });
            user = response.data.result;
            document.cookie = 'sid=' + response.data.sid

        },

        async mainView() {
            const headers = {'Authorization': 'Token \t' + user.token}
            const mainMenuUrl = this.url + '/app/v1/main/';
            const response = await axios.post(mainMenuUrl, {}, {headers}).catch(function (error) {
                this.data.errors = error.toJSON()
                console.log(error.toJSON());
            });
            return response.data.result
        },
        async pickings(pick_type) {
            const headers = {'Authorization': 'Token \t' + user.token}
            const pickingsUrl = this.url + '/app/v1/pickings/';
            const response = await axios.post(pickingsUrl, {'picking_type': pick_type}, {headers}).catch(function (error) {
                this.data.errors = error.toJSON()
                console.log(error.toJSON());
            });
            return response.data.result
        },
        async picking(pk) {
            const headers = {'Authorization': 'Token \t' + user.token}
            const pickingUrl = this.url + '/app/v1/picking/';
            const response = await axios.post(pickingUrl, {'picking_id': pk}, {headers}).catch(function (error) {
                this.data.errors = error.toJSON()
                console.log(error.toJSON());
            });
            return response.data.result
        },
        async scan_product_with_picking(payload) {
            const headers = {'Authorization': 'Token \t' + user.token}
            const pickingUrl = this.url + '/app/v1/scan_product_with_picking/';
            const response = await axios.post(pickingUrl, {'payload': payload}, {headers}).catch(function (error) {
                this.data.errors = error.toJSON()
                console.log(error.toJSON());
            });
            return response.data.result
        },
        async pickingForceComplete(pk) {
            const headers = {'Authorization': 'Token \t' + user.token}
            const pickingForceCompleteUrl = this.url + '/app/v1/picking_force_complete/';
            const response = await axios.post(pickingForceCompleteUrl, {'picking_id': pk}, {headers}).catch(function (error) {
                this.data.errors = error.toJSON()
                console.log(error.toJSON());
            });
            return response.data.result
        },
        async get_stock_move(sm) {
            const headers = {'Authorization': 'Token \t' + user.token}
            const stockMoveUrl = this.url + '/app/v1/get_stock_move/';
            const response = await axios.post(stockMoveUrl, {'stock_move_id': sm}, {headers}).catch(function (error) {
                this.data.errors = error.toJSON()
                console.log(error.toJSON());
            });
            return response.data.result
        },
        async get_or_create_object(payload) {
            const headers = {'Authorization': 'Token \t' + user.token}
            const getOrCreateObjectUrl = this.url + '/app/v1/get_or_create_object/';
            const response = await axios.post(getOrCreateObjectUrl, payload, {headers}).catch(function (error) {
                this.data.errors = error.toJSON()
                console.log(error.toJSON());
            });
            return response.data.result
        },
        async stockMoveLineDone(doneData) {
            const headers = {'Authorization': 'Token \t' + user.token}
            const stockMoveLineDoneUrl = this.url + '/app/v1/stock_move_line_done/';
            const response = await axios.post(stockMoveLineDoneUrl, doneData, {headers}).catch(function (error) {
                this.data.errors = error.toJSON()
                console.log(error.toJSON());
            });
            return response.data.result
        },
        pullerInterval: 5000,
        async poll(url) {
            try {
                if (!this.url){
                    this.url = url
                }
                const headers = {'Authorization': 'Token \t' + user.token}
                const pollUrl = this.url + '/app/v1/stock_poll/';
                const response = await axios.post(pollUrl, {'cursor': this.pollCursor}, {headers}).catch(function (error) {
                    this.data.errors = error.toJSON()
                    console.log(error.toJSON());
                });
                //console.log(store)
                this.pullerInterval = 1
                store.commit('ordersUpdate', response.data.result.orders)
                //console.log(store.state.orders)
                await new Promise(r => setTimeout(r, 5000));
                await this.poll()
            }
            catch(e){
                const interval = this.pullerInterval
                this.pullerInterval = this.pullerInterval < 16 ? this.pullerInterval * 2: 16
                setTimeout(await this.poll, interval, this.url)
                console.log(e)

            }
        }
    },
    data: {
        errors: false
    }
}


