<script>
import lavkach_api from "@/lavkach_api";


export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'Picking',
  data: function () {
    return {
      packman_show: true,
      scanner: '',
    }
  },
  watch: {
    async scanner() {
      console.log(this.scanner)
    },
  },

  async mounted() {
    this.pk = this.$route.params.pk
  },
  computed: {
    picking() {
      return this.$store.getters.ordersById(this.$route.params.pk)
    }
  },
  methods: {
    async complete() {
      console.log('complete')
      const data = await lavkach_api.methods.pickingForceComplete(this.pk);
      if (data.status === true) {
        await this.$router.push({name: 'PickingType', params: {pick_type: this.pick.picking_type_id[0]}})
      }
    }
  }
}

</script>

