import { createWebHistory, createRouter } from "vue-router";
import Picking from "../components/Picking";
import PickingType from "@/components/PickingType";
import StockMove from "@/components/StockMove";
import Main from "../components/Main";
import Index from "../components/Login";


const routes = [
    {
        path: "/app/",
        name: "Login",
        component: Index,
    },
    {
        path: "/pickingtype/:pick_type/picking/:pk",
        name: "Picking",
        component: Picking,
    },
    {
        path: "/pickingtype/:pick_type/picking/:pk/stockmove/:sm",
        name: "StockMove",
        component: StockMove,
    },
    {
        path: "/pickingtype/:pick_type",
        name: "PickingType",
        component: PickingType,
    },
    {
        path: "/main",
        name: "Main",
        component: Main,
    },
];

const router = createRouter({
    history: createWebHistory(),
    routes,
});

export default router;